<template>
  <div class="notice">
    <el-dialog
      theame
      center
      title=""
      :visible="dialogVisiblea"
      width="580px"
      top="15%"
      :modal="false"
      @close="closDialos"
    >
      <div class="menOrde">
        <el-tabs
          v-model="activeName"
          :tab-position="tabPosition"
          style="height: 366px;"
          @tab-click="handleClick"
        >
          <el-tab-pane label="" name="first">
            <div class="sub_body">
              <div class="phone-number"></div>
              <div class="number-updat">
                <div class="phon-serve">
                  <span>{{mobile}}</span>
                </div>
                <div class="updt-serve">
                  <div class="upd-div" @click="upda()">
                    <img src="~@/assets/images/stting/upd.png" style="width:14px;height:14px" />
                    <span>&nbsp;</span>
                  </div>
                </div>
              </div>
              <div class="email-number"></div>
              <div class="number-updat">
                <div class="phon-serves">
                  <span>{{email || ""}}</span>
                </div>
                <!-- <div class="updt-serves">
                  <div class="upd-div" @click="updEmail()">
                    <img src="~@/assets/images/stting/upd.png" style="width:14px;height:14px" />
                    <span>&nbsp;{{email?'': ''}}</span>
                  </div>
                </div> -->
              </div>
            </div>
          </el-tab-pane>
          <el-tab-pane label="" name="second">
            <div class="sub_body">
              <div class="phone-number"></div>
              <div class="number-updat">
                <div class="phon-serve">
                  <span></span>
                </div>
                <div class="updt-serve" @click="uplognPasword()">
                  <div class="upd-div">
                    <img src="~@/assets/images/stting/upd.png" style="width:14px;height:14px" />
                    <span>&nbsp;</span>
                  </div>
                </div>
              </div>
              <div class="email-number"></div>
              <div class="number-updat">
                <div class="phon-serves">
                  <span></span>
                </div>
                <div class="updt-serves">
                  <div class="upd-div" @click="uptionPassword()">
                    <img src="~@/assets/images/stting/upd.png" style="width:14px;height:14px" />
                    <span>&nbsp;</span>
                  </div>
                </div>
              </div>
              <!-- <div class="lock-setting">
                <div class="lock-tips">
                  <div class="lock-time">
                    <span></span>
                    <el-input
                      v-model="lockTime"
                      maxlength="4"
                      @change="changeLockData"
                      :disabled="!lockSwitch"
                    ></el-input>
                    <span></span>
                  </div>
                  <div class="lock-tips-text"></div>
                </div>
                <el-switch
                  class="lock-btn"
                  v-model="lockSwitch"
                  @change="changeLockData"
                  active-color="#DEA567"
                  inactive-color="#9195AC"
                ></el-switch>
              </div>-->
            </div>
          </el-tab-pane>
          <el-tab-pane label="" name="third">
            <div class="newest" v-if="!updateData.version || !needUpdate">
              <img src="~@/assets/logo.png" alt class="logo" />
              <p></p>
              <p>{{config.version}}</p>
              <p>~</p>
            </div>
            <div class="new-version" v-if="updateData.version && needUpdate">
              <p></p>
              <p>V{{updateData.version}}</p>
              <div class="update-content" v-if="progress <= 0">
                <p class="t-title"></p>
                <div class="content-text" v-html="updateData.context"></div>
              </div>
              <div v-if="progress > 0">
                <div class="update-status">V {{updateData.version}}</div>
                <el-progress
                  class="processing"
                  :stroke-width="6"
                  :percentage="progress"
                  :show-text="false"
                ></el-progress>
                <div class="percent-num">{{progress}}%</div>
              </div>
            </div>
            <el-button
              class="update-btn log-out"
              @click="checkUpdate"
              :class="{gray: isUpdating}"
              hover
              v-if="updateData.version && needUpdate"
            ></el-button>
          </el-tab-pane>
          <el-tab-pane label="" name="fourth">
            <div class="company-info">
              <h3 class="name"></h3>
              <p>1() 2() (: AVW468)</p>
              <p>10:;</p>
              <p> ,</p>
            </div>
          </el-tab-pane>
          <el-tab-pane label="" name="fifth">
            <!-- <div class="log-out" hover @click="logOut"></div> -->
          </el-tab-pane>
        </el-tabs>
      </div>
    </el-dialog>
    <PhoneFlie ref="phoneFlie" />
    <Email ref="email" />
    <lognPasswords ref="lognPasswords" />
    <tionPassword ref="tionPassword" />
  </div>
</template>

<script>
import PhoneFlie from "./subfile/phone";
import Email from "./subfile/email";
import lognPasswords from "./subfile/lognPassword";
import tionPassword from "./subfile/tionPassword";
import { reLogin, getPersonalData } from "@/utils";
const { ipcRenderer: ipc } = require("electron");
import config from "../../../../package.json";
// console.log(config.version);
export default {
  name: "notice",
  components: {
    PhoneFlie,
    Email,
    lognPasswords,
    tionPassword
  },
  data() {
    return {
      dialogVisiblea: false,
      tabPosition: "left",
      lockSwitch: true,
      lockTime: 30,
      updateStatus: "",
      progress: 0,
      activeName: "first",
      updateData: {},
      config: config,
      isUpdating: false
    };
  },
  mounted() {
    this.setLockData();
  },
  watch: {
    // activeName(val) {
    //   if (val === "fifth") {
    //     setTimeout(() => {
    //       this.logOut();
    //     }, 300);
    //   }
    // }
  },
  computed: {
    personalInfo(){
      return this.$store.state.system.userInfo;
    },
    email() {
      return this.personalInfo.email;
    },
    mobile() {
      let mobile = this.personalInfo.mobile || "";
      let strMobile =
        mobile.substring(0, 3) + "*****" + mobile.substring(7, 11);
      return strMobile;
    },
    needUpdate() {
      if (this.updateData.version) {
        let ver = this.updateData.version.split(".");
        let cur = this.config.version.split(".");
        for (let i = 0; i < ver.length; i++) {
          if (Number(ver[i]) > Number(cur[i])) {
            return true;
          }
        }
        return false;
      } else {
        return false;
      }
    }
  },
  methods: {
    handleClick(tab) {
      if (this.activeName === "fifth") {
        this.logOut();
      }
      if (this.activeName === "third") {
        //  /app/version/getAppVersion
        this.axios({
          url: "/app/version/getAppVersion?type=2",
          method: "post",
          params: {},
          type: "host3"
        }).then(res => {
          this.updateData = res.data.ret || {};
        });
      }
    },
    checkUpdate() {
      // 
      let _this = this;
      if (this.isUpdating) {
        return false;
      }
      this.isUpdating = true;
      ipc.on("message", (message, data) => {
        console.log(message, data);
        _this.updateStatus = data.message.msg;
        if (data.message.status === 3) {
          // 
          _this.progress = parseInt(data.data.percent);
        }
      });
      ipc.send("update");
    },
    logOut() {
      this.$confirm("", "", {
        cancelButtonText: ""
      })
        .then(res => {
          reLogin();
        })
        .catch(err => {});
    },
    setLockData() {
      let lockData = localStorage.getItem("lockData");
      if (lockData) {
        lockData = JSON.parse(localStorage.getItem("lockData"));
      } else {
        lockData = {
          lockSwitch: true,
          lockTime: 30
        };
      }
      this.lockSwitch = Boolean(lockData.lockSwitch);
      this.lockTime = lockData.lockTime;
    },
    changeLockData() {
      let lockTime = String(this.lockTime);
      lockTime = lockTime.replace(/[^\.\d]/g, "").replace(/\./g, "");
      if (Number(lockTime) <= 0) {
        lockTime = 1;
      }
      this.lockTime = Number(lockTime);
      let o = {
        lockSwitch: this.lockSwitch,
        lockTime: this.lockTime
      };
      localStorage.setItem("lockData", JSON.stringify(o));
    },
    show() {
      if (!this.personalInfo.mobile) {
        getPersonalData();
      }
      this.dialogVisiblea = true;
      this.isUpdating = false;
    },
    getData() {
      getPersonalData();
      setTimeout(() => {
        this.show();
      }, 2000);
    },
    upda() {
      console.log("");
      this.$refs.phoneFlie.show();
    },
    updEmail() {
      console.log("");
      this.$refs.email.show();
    },
    uplognPasword() {
      console.log("");
      this.$refs.lognPasswords.show();
    },
    uptionPassword() {
      console.log("");
      this.$refs.tionPassword.show();
    },
    closDialos() {
      this.dialogVisiblea = false;
      this.$emit("close");
    }
  }
};
</script>

<style lang="scss" scoped>
.notice {
  .menOrde {
    height: 100%;
    @include flex-box;
    .sub_body {
      width: 460px;
      height: 100%;
      .phone-number {
        color: $defaultColor;
        height: 31px;
        margin-top: 28px;
        padding-top: 5px;
        margin-left: 30px;
      }
      .email-number {
        color: $defaultColor;
        height: 35px;
        margin-top: 41px;
        padding-top: 5px;
        margin-left: 30px;
      }
      .number-updat {
        width: 100%;
        height: 30px;
        @include flex-box;
        .phon-serve {
          width: 100px;
          height: 20px;
          margin-left: 30px;
          > span {
            color: $white;
            @include add-size($font_size_14);
            line-height: 20px;
          }
        }
        .phon-serves {
          width: 150px;
          height: 20px;
          margin-left: 30px;
          > span {
            color: $white;
            @include add-size($font_size_14);
            line-height: 20px;
          }
        }
        .updt-serve {
          width: 218px;
          height: 20px;
          .upd-div {
            width: 70px;
            height: 20px;
            cursor: pointer;
            background: #9195ac;
            border-radius: 4px;
            margin-left: 140px;
            text-align: center;
            padding-top: 2px;
            > span {
              color: $white;
              @include add-size($font_size_14);
            }
          }
        }
        .updt-serves {
          width: 210px;
          height: 20px;
          .upd-div {
            width: 70px;
            height: 20px;
            cursor: pointer;
            background: #9195ac;
            border-radius: 4px;
            margin-left: 90px;
            text-align: center;
            padding-top: 2px;
            > span {
              color: $white;
              @include add-size($font_size_14);
            }
          }
        }
      }
      .lock-setting {
        color: $defaultColor;
        @include add-size($font_size_14);
        @include flex-box;
        line-height: 28px;
        margin-top: 40px;
        margin-left: 30px;
        .lock-tips {
          margin-right: 47px;
          .lock-time {
            @include flex-box;
            /deep/ .el-input {
              width: auto;
              .el-input__inner {
                width: 60px;
                height: 14px;
                color: $defaultColor;
                background-color: inherit;
                border: none;
                border-radius: 0;
                line-height: 28px;
                border-bottom: 1px solid #576373;
                padding: 0;
                text-align: center;
                margin: 0 10px;
              }
            }
          }
          .lock-tips-text {
          }
        }
      }
    }
  }
  .log-out {
    width: 120px;
    @include linear;
    line-height: 30px;
    text-align: center;
    margin: 200px 160px 0;
    border-radius: 4px;
    color: $white;
    cursor: pointer;
  }
  /deep/
    .el-dialog__wrapper[theame]
    .el-dialog
    .el-dialog__header
    .el-dialog__title {
    color: $defaultColor;
  }
  /deep/ .el-tabs__item.is-active {
    color: $activeColor;
    background-color: #1d242e;
    margin-left: -20px;
  }
  /deep/.el-tabs__nav-scroll {
    background-color: #252e3b;
    width: 120px;
  }
  /deep/.el-tabs__item {
    color: $defaultColor;
  }
  /deep/.el-tabs--left .el-tabs__item.is-left {
    text-align: center;
  }
  /deep/.el-tabs__nav-wrap::after {
    display: none;
  }
  /deep/.el-tabs--left .el-tabs__active-bar.is-left,
  .el-tabs--left .el-tabs__nav-wrap.is-left::after {
    left: 0;
  }
  /deep/.el-tabs--left .el-tabs__active-bar.is-left {
    width: 4px;
    height: 20px !important;
    margin-left: 10px;
    margin-top: 10px;
  }

  .update-btn {
    padding: 0;
    margin-top: 100px;
    &.gray {
      background: #999;
    }
  }

  .newest {
    text-align: center;
    width: 440px;
    padding-top: 55px;
    color: $defaultColor;
    .logo {
      width: 70px;
      height: 70px;
      border-radius: 50%;
    }
    p {
      &:nth-child(2) {
        margin-top: 20px;
      }
      &:nth-child(3) {
        margin-top: 20px;
      }
      &:nth-child(4) {
        margin-top: 65px;
      }
    }
  }
  .new-version {
    width: 400px;
    height: 277px;
    margin: 0 20px;
    border-bottom: 1px solid $borderColor;
    > p {
      &:nth-child(1) {
        padding-top: 36px;
        color: $white;
        @include add-size($font_size_16);
      }
      &:nth-child(2) {
        color: $white;
        padding-top: 9px;
        @include add-size($font_size_16);
      }
    }
    .update-content {
      margin-top: 39px;
      color: $defaultColor;
      @include add-size($font_size_14);
      .t-title {
        margin-bottom: 7px;
      }
      .content-text {
        line-height: 30px;
        max-height: 120px;
        overflow-y: auto;
      }
    }
    .update-status {
      color: $defaultColor;
      margin-top: 49px;
    }
    .processing {
      margin-top: 30px;
    }
    .percent-num {
      margin-top: 10px;
      text-align: center;
      color: $white;
    }
  }
  .update-btn {
    margin-left: auto;
    margin-top: 18px;
    margin-right: 20px;
    border: none;
    display: block;
  }
  .company-info {
    padding: 0 20px;
    color: $defaultColor;
    .name {
      @include add-size($font_size_16);
      padding: 30px 0 10px;
      font-weight: normal;
    }
    p {
      text-indent: 2em;
      line-height: 24px;
      @include add-size($font_size_14);
    }
  }
}
</style>
