<template>
  <div class="ipo-mask">
    <el-dialog theame center title="" :visible="isShow" width="690px" top="8%" @close="close">
      <div class="ipo-content">
        <ul class="subscribe-ul">
          <li class="subscribe-li">
            <div class="subscribe-li_name"></div>
            <div class="subscribe-li_c">{{ipoData.nameGb}} {{ipoData.productCode}}</div>
          </li>
          <li class="subscribe-li">
            <div class="subscribe-li_name"></div>
            <div class="subscribe-li_c">{{ipoData.account}}</div>
          </li>
          <li class="subscribe-li">
            <div class="subscribe-li_name"></div>
            <div class="subscribe-li_c">
              <span
                class="flag-li"
                :class="{active: applyWayStatus === 0}"
                @click="applyWayStatus = 0"
                v-if="ipoData.financingStatus == 0"
              ></span>
              <span
                class="flag-li"
                :class="{active: applyWayStatus === 1}"
                @click="applyWayStatus = 1"
                v-if="ipoData.international == 0"
              ></span>
              <span
                class="flag-li"
                :class="{active: applyWayStatus === 2}"
                @click="applyWayStatus = 2"
                v-if="ipoData.cashStatus == 0"
              ></span>
            </div>
          </li>
          <li class="subscribe-li" v-if="applyWayStatus === 0">
            <div class="subscribe-li_name"></div>
            <div class="subscribe-li_c">{{ipoData.financeRate}}</div>
          </li>
          <li class="subscribe-li" v-if="applyWayStatus === 1">
            <div class="subscribe-li_name"></div>
            <el-input v-model="ipoData.actFinance" :placeholder="'' + ipoData.minFinance + ''"></el-input>
          </li>
          <li class="subscribe-li" v-if="applyWayStatus !== 1">
            <div class="subscribe-li_name"></div>
            <div class="subscribe-li_c">
              <el-select v-model="quantity" @change="changeQuantity">
                <el-option
                  v-for="item in ipoData.eipoListVoList"
                  :key="item.issuedShares"
                  :label="item.label"
                  :value="item.issuedShares"
                ></el-option>
              </el-select>
            </div>
          </li>
          <li class="subscribe-li">
            <div class="subscribe-li_name"></div>
            <div class="subscribe-li_c">{{ipoData.cashBalance | moneyFormat}} </div>
          </li>
          <li class="subscribe-li" v-if="applyWayStatus !== 1">
            <div class="subscribe-li_name"></div>
            <div
              class="subscribe-li_c"
            >{{(ipoData.priceAllo - ipoData.proceduresMoney || 0) | moneyFormat}} </div>
          </li>
          <li class="subscribe-li" v-if="applyWayStatus !== 1">
            <div class="subscribe-li_name"></div>
            <div
              class="subscribe-li_c"
            >{{( Number(ipoData.serviceCharge) + ipoData.proceduresMoney || 0) | moneyFormat}} </div>
          </li>
          <li class="subscribe-li" v-if="applyWayStatus !== 1">
            <div class="subscribe-li_name"></div>
            <div
              class="subscribe-li_c"
            >{{( Number(ipoData.serviceCharge) + ipoData.priceAllo || 0) | moneyFormat}} </div>
          </li>
        </ul>
        <div class="tips">
          <div class="tips-title"></div>
          <div class="tips-content">
            <p>1.100</p>
            <p>2.</p>
            <p>,,00:00,,</p>
            <p>3.</p>
            <p>,21:00</p>
            <p>4.</p>
            <p>a)1%+0.0027%+0.005%+100</p>
            <p>b)x,</p>
            <p>c),,</p>
          </div>
        </div>
        <div class="submit-bar">
          <div class="protocol">
            <div class="radio" :class="{active: radio}" @click="radio = !radio"></div>
            <div class="protocol-text">
              
              <span @click="showProtocol = true"></span>
            </div>
          </div>
          <button class="submit action" hover @click="submitIPO"></button>
        </div>
      </div>
    </el-dialog>
    <el-dialog
      theame
      center
      title=""
      :visible="showProtocol"
      width="690px"
      top="11%"
      @close="showProtocol = false"
    >
      <div class="protocol-content">
        <div class="protocol-title"></div>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isShow: false,
      quantity: "",
      radio: true,
      showProtocol: false,
      ipoData: {},
      applyWayStatus: 2
    };
  },
  watch: {
    applyWayStatus(val) {
      this.applyWay(val);
    }
  },
  methods: {
    submitIPO() {
      try {
        if (!this.radio) {
          throw Error("");
        }
        if (this.applyWayStatus != 1 && this.quantity <= 0) {
          throw Error("");
        }
        let ipoData = this.ipoData;
        // console.log(ipoData);
        if (
          this.applyWayStatus == 1 &&
          Number(ipoData.actFinance || 0) < Number(ipoData.minFinance)
        ) {
          throw Error("");
        }
        if (
          this.applyWayStatus == 1 &&
          Number(ipoData.actFinance || 0) > ipoData.cashBalance
        ) {
          throw Error("");
        }

        if (ipoData.cashBalance < Number(ipoData.priceAllo)) {
          throw Error("");
        }
        // GET /eipo/eipoData
        return;
        this.axios({
          url: "/eipo/save",
          method: "post",
          params: {
            applyWay: this.applyWayStatus,
            account: ipoData.account,
            issuedShares: this.quantity,
            ipoId: this.liData.ipoId,
            nameGb: ipoData.nameGb,
            productCode: ipoData.productCode,
            serviceCharge: ipoData.serviceCharge,
            totalMoney:
              this.applyWayStatus == 1 ? ipoData.actFinance : ipoData.priceAllo,
            TransactionFee: ipoData.proceduresMoney
            // signatureUrl: ""
          },
          type: "host3"
        }).then(res => {
          this.$message({ message: "", type: "success" });
          this.close();
        });
      } catch (err) {
        this.$message({ message: err.message, type: "error" });
      }
    },
    show(row) {
      this.isShow = true;
      (this.quantity = ""), (this.liData = row);
      // this.getIpoInfo();
      this.applyWay(2);
    },
    getIpoInfo() {
      let liData = this.liData;
      // GET /eipo/selectEipo
      this.axios({
        url: "/eipo/selectEipoLever",
        params: {
          ipoId: liData.ipoId
        },
        type: "host3"
      })
        .then(res => {
          let data = res.data.ret;
          data.eipoListVoList.forEach(a => {
            a.label = a.issuedShares + " " + " " + a.priceAllo + " ";
          });
          this.ipoData = res.data.ret;
          this.applyWay(2);
        })
        .catch(err => {
          if (err.data.code === 9999) {
            this.$store.dispatch("updateAccountData", {});
            this.close();
          }
        });
    },
    applyWay(way) {
      // GET /eipo/applyWay
      let liData = this.liData;
      //  0  1  2 
      this.axios({
        url: "/eipo/applyWay",
        params: {
          ipoId: liData.ipoId,
          channel: 4,
          applyWay: way
        },
        type: "host3"
      }).then(res => {
        let data = res.data.ret;
        if (data.eipoListVoList) {
          data.eipoListVoList.forEach(a => {
            a.label = a.issuedShares + " " + " " + a.priceAllo + " ";
          });
          this.ipoData = res.data.ret;
        }
      });
    },
    changeQuantity(e) {
      this.ipoData.eipoListVoList.forEach(a => {
        if (a.issuedShares === e) {
          this.ipoData.priceAllo = a.priceAllo;
          this.ipoData.proceduresMoney = a.proceduresMoney;
        }
      });
    },
    close() {
      this.isShow = false;
    }
  }
};
</script>

<style lang="scss" scoped>
.ipo-mask {
  /deep/ .el-dialog {
    .el-dialog__header {
      .el-dialog__title {
        color: $defaultColor !important;
        @include add-size($font_size_14);
      }
    }
  }
  .ipo-content {
    .subscribe-ul {
      @include add-size($font_size_14);
      padding: 30px 0 10px;
      @include flex-box;
      flex-wrap: wrap;
      .subscribe-li {
        padding: 10px 30px;
        width: 50%;
        @include flex-center;
        color: $white;
        .subscribe-li_name {
          width: 110px;
          color: $defaultColor;
        }
        .subscribe-li_c {
          @include flex-center;
          &.is-right {
            margin-left: auto;
          }
          .quantity {
            position: relative;
            /deep/ .el-input__inner {
              background-color: inherit;
              border: none;
              border-radius: 0;
              border-bottom: 1px solid #576373;
              width: 200px;
              color: $white;
              padding: 0;
              text-align: center;
              height: 28px;
            }
            i {
              @include add-size($font_size_14);
              position: absolute;
              color: $defaultColor;
              left: 15px;
              top: 6px;
              z-index: 10;
              cursor: pointer;
              &:hover {
                color: $activeColor;
              }
              &.plus {
                left: auto;
                right: 15px;
              }
            }
          }
          .per-hand {
            span {
              display: inline-block;
              width: 100px;
              text-align: center;
            }
          }
          /deep/ .el-button {
            width: 58px;
            height: 24px;
            background-color: inherit;
            border: 1px solid #767a92;
            padding: 0;
            @include add-size($font_size_12);
            span {
              line-height: 22px;
              color: $defaultColor;
            }
            &:hover {
              background-color: $activeColor;
              border-color: $activeColor;
              span {
                color: $white;
              }
            }
          }
          .flag-li {
            width: 70px;
            height: 24px;
            line-height: 22px;
            border: 1px solid $defaultColor;
            color: $defaultColor;
            text-align: center;
            margin-right: 6px;
            cursor: pointer;
            &:last-child {
              margin-right: 0;
            }
            &.active {
              color: $white;
              background: $activeColor;
            }
          }
        }
        &.line {
          padding: 0;
          div {
            height: 1px;
            width: 100%;
            background-color: #323840;
          }
        }
      }
      /deep/ .el-select {
        width: 190px;
        input {
          background: $baseBg;
          border: none;
          border-radius: 0;
          color: $white;
          height: 30px;
          line-height: 30px;
        }
        .el-select__caret {
          line-height: 30px;
          &::before {
            content: "\e78f";
          }
        }
      }
      /deep/ .el-input {
        background-color: inherit;
        .el-input__inner {
          background-color: inherit;
          border-radius: 0;
          height: 36px;
        }
      }
    }
    .tips {
      color: $defaultColor;
      padding: 0 30px 20px;
      @include add-size($font_size_14);
      .tips-title {
        line-height: 30px;
      }
      .tips-content {
        line-height: 24px;
      }
    }
    .submit-bar {
      @include flex-center;
      border-top: 1px solid #323840;
      height: 70px;
      padding: 0 30px;
      .protocol {
        @include flex-center;
        .radio {
          width: 12px;
          height: 12px;
          border-radius: 4px;
          border: 1px solid $activeColor;
          position: relative;
          cursor: pointer;
          &.active {
            &::before {
              content: "";
              position: absolute;
              left: 50%;
              top: 50%;
              width: 6px;
              height: 6px;
              background-color: $activeColor;
              margin-left: -3px;
              margin-top: -3px;
              border-radius: 2px;
            }
          }
        }
        .protocol-text {
          margin-left: 10px;
          span {
            color: $activeColor;
            cursor: pointer;
          }
        }
      }

      .submit {
        width: 100px;
        height: 30px;
        line-height: 30px;
        padding: 0;
        border-radius: 2px;
        cursor: pointer;
        margin: 0 0 0 auto;
        @include add-size($font_size_14);
      }
    }
  }
  .protocol-content {
    padding: 0 30px 60px;
    color: $white;
    .protocol-title {
      padding-top: 40px;
      padding-bottom: 23px;
      text-align: center;
    }
    p {
      @include add-size($font_size_12);
      text-indent: 2em;
      line-height: 24px;
    }
  }
}
</style>