<template>
  <div class="forget">
    <el-dialog
      theame
      title=""
      :visible="showForget"
      width="470px"
      height="350px"
      top="15%"
      class="account-dialog"
      :modal="false"
      :close-on-click-modal="false"
      @close="close"
    >
      <div class="header" slot="title">
        <i class="el-icon-arrow-left back" @click="close"></i>
      </div>
      <div class="forget-content" v-if="type === 'forget'">
        <!--         <div class="step-title"></div>
        <p
          class="text-content"
        >--  400-1166-913 </p>-->
        <el-form :model="forgetData" :rules="formRules" ref="forgetData">
          <el-form-item label="" class="account" prop="account" v-show="stepNum === 0">
            <el-select
              placeholder=""
              v-model="forgetData.account"
              @change="forgetData.password = ''"
            >
              <el-option
                :label="op.value"
                :value="op.value"
                v-for="op in accountSelectData"
                :key="op.value"
              >
                <span class="option-value">{{ op.value }}</span>
                <span class="option-label">{{ op.label }}</span>
              </el-option>
            </el-select>

            <!-- <i class="icon"></i> -->
          </el-form-item>
          <el-form-item label="" prop="mobile" class="password" v-show="stepNum === 0">
            <el-input class="password-value" v-model="forgetData.mobile" readonly></el-input>
          </el-form-item>
          <el-form-item label="" prop="vcode" class="password" v-show="stepNum === 0">
            <el-input
              class="password-value"
              v-model="forgetData.vcode"
              placeholder=""
              maxlength="6"
            ></el-input>
            <div
              class="get-vcode"
              :class="{'has-send': residueTime>0}"
              @click="getVCode('forgetData', 3)"
            >{{residueTime>0?residueTime+'s':''}}</div>
          </el-form-item>
          <el-form-item label="" prop="password" class="password" v-show="stepNum === 1">
            <el-input
              class="password-value"
              :type="showPassword1"
              v-model="forgetData.password1"
              maxlength="20"
              placeholder=""
              @keyup.enter.native="forgetSubmit"
            ></el-input>
            <i
              class="icon"
              :class="{password:showPassword1 ==='password'}"
              @click="showPassword1 === 'password'?showPassword1 = 'text':showPassword1 ='password'"
            ></i>
          </el-form-item>
          <el-form-item label="" prop="password" class="password" v-show="stepNum === 1">
            <el-input
              class="password-value"
              :type="showPassword2"
              v-model="forgetData.password2"
              maxlength="20"
              placeholder=""
              @keyup.enter.native="forgetSubmit"
            ></el-input>
            <i
              class="icon"
              :class="{password:showPassword2 ==='password'}"
              @click="showPassword2 === 'password'?showPassword2 = 'text':showPassword2 ='password'"
            ></i>
          </el-form-item>
          <el-button class="action login-submit" hover @click="nextStep" v-show="stepNum === 0"></el-button>
          <el-button
            class="action login-submit"
            hover
            @click="forgetSubmit"
            v-show="stepNum === 1"
          ></el-button>
          <el-button class="action login-submit" hover @click="lastStep" v-show="stepNum === 1"></el-button>
        </el-form>
      </div>
      <div class="forget-content" v-if="type === 'statement'">
        <div class="step-title"></div>
        <p class="text-content" v-html="content"></p>
      </div>
      <div class="forget-content" v-if="type === 'ewm'">
        <div class="step-title"></div>
        <div class="ewm-img">
          <!-- <img src="http://h5.lc913.com/web/openAccount/ewm.png" alt /> -->
          <img src="http://www.easy913.com/image/erweima118.png" alt />
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "forget",
  components: {},
  data() {
    return {
      showForget: false,
      stepNum: 0,
      type: "forget",
      forgetData: {
        account: "",
        password1: "",
        password2: "",
        mobile: "",
        vcode: "",
        areaCode: ""
      },
      showPassword1: "password",
      showPassword2: "password",
      accountSelectData: [],
      formRules: {
        mobile: [
          { required: false, message: "", trigger: "blur" },
          {
            pattern: /^[1][3-9]\d{9}$|^([6|9])\d{7}$|^[0][9]\d{8}$|^[6]([8|6])\d{5}$/,
            message: "",
            trigger: "blur"
          }
        ],
        vcode: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        password: [{ required: false, message: "", trigger: "blur" }]
      },
      residueTime: 0,
      content: `<p>()() ()</p>
                <p>1. </p>
                <p></p>
                <p></p>
                <p></p>
                <p></p>
                <p></p>
                <p></p>
                <p></p>
                <p>2. </p>
                <p></p>
                <p>3.</p>
                <p>3.1 </p>
                <p>()</p>
                <p>(i) </p>
                <p>(ii) </p>
                <p>(iii) /()</p>
                <p>(iv) </p>
                <p>(v) () </p>
                <p>(vi) </p>
                <p>3.2 </p>
                <p></p>
                <p>(i) </p>
                <p>(ii) </p>
                <p>(iii) </p>
                <p>(iv) </p>
                <p>(v) </p>
                <p>(vi) ()(a)(b)/(c)()</p>
                <p>(vii) </p>
                <p>(viii) </p>
                <p>(ix) </p>
                <p>(x) </p>
                <p>3.3 </p>
                <p>/()</p>
                <p>(i) </p>
                <p>(ii) /</p>
                <p>3.4 </p>
                <p></p>
                <p>4. </p>
                <p>() </p>
                <p>(i) </p>
                <p>(ii) </p>
                <p>(iii) </p>
                <p>(iv) </p>
                <p>(v) 3.2</p>
                <h3></h3>
                <p>1 ( ) ()</p>
                <p>2  / </p>
                <p>3 /</p>
                <p>4  //   </p>
                <p>5/  (</p>
                <p>6</p>
                <p>7  </p>
                <p>8 </p>
                <p>9   </p>
                <p>10 </p>
                <p>11 -    /  -    </p>`
    };
  },
  mounted() {},
  watch: {
    stepNum(val) {
      if (val === 0) {
        this.forgetData.vcode = "";
      }
      if (val === 1) {
        this.forgetData.password1 = "";
        this.forgetData.password2 = "";
      }
    }
  },
  methods: {
    forgetSubmit() {
      try {
        let forgetData = this.forgetData;
        // if (!forgetData.password1) {
        //   throw new Error("");
        // }
        if (forgetData.password1.length < 8) {
          throw new Error("8");
        }
        if (!/^[A-Z]*$/.test(forgetData.password1[0])) {
          throw new Error("");
        }
        if (/[\u4e00-\u9fa5]/.test(forgetData.password1)) {
          throw new Error("");
        }
        if (/\s/.test(forgetData.password1)) {
          throw new Error("");
        }
        if (forgetData.password1 != forgetData.password2) {
          throw new Error("");
        }
        let channel = 0;
        this.accountSelectData.forEach(a => {
          if (a.value === forgetData.account) {
            if (a.label === "") {
              channel = 10;
            }
          }
        });
        let url = "/appcustomer/changeTransactionPassword.do";
        // if(channel === 0){

        // }
        this.axios({
          url: url,
          method: "post",
          params: {
            channel: channel, //0   10 
            type: 3,
            newPassword: forgetData.password1
          }
        })
          .then(res => {
            this.$message({
              message: "",
              type: "success"
            });
            this.showForget = false;
            this.forgetData = {
              account: "",
              password1: "",
              password2: "",
              mobile: "",
              vcode: "",
              areaCode: ""
            };
            this.stepNum = 0;
          })
          .catch(err => {});
      } catch (err) {
        this.$message({
          message: err.message,
          type: "error"
        });
      }
    },
    lastStep() {
      this.$confirm("", "", {
        cancelButtonText: ""
      })
        .then(res => {
          this.stepNum = 0;
        })
        .catch(err => {
          console.log(err);
        });
    },
    startTimer() {
      // 
      this.residueTime = 60;
      this.timer = setInterval(() => {
        this.residueTime--;
        if (this.residueTime < 0) {
          this.residueTime = 0;
          clearInterval(this.timer);
        }
      }, 1000);
    },
    getVCode(t, type) {
      // 
      if (this.residueTime > 0) {
        return false;
      }
      let flag = false;
      this.$refs[t].validateField("mobile", valid => {
        flag = !valid;
      });
      if (flag) {
        let target = this[t];
        let mobile = target.mobile;
        this.startTimer();
        this.axios({
          url: "/messageCode/getCode.do",
          params: {
            mobile: mobile,
            type: type
          }
        })
          .then(res => {
            this.$message({
              message: "",
              type: "success"
            });
          })
          .catch(err => {});
      }
    },
    nextStep() {
      // POST /appcustomer/confirmIdentification.do
      let forgetData = this.forgetData;
      let channel = 0;
      this.accountSelectData.forEach(a => {
        if (a.value === forgetData.account) {
          if (a.label === "") {
            channel = 10;
          }
        }
      });
      let flag = false;
      this.$refs.forgetData.validateField("vcode", valid => {
        flag = !valid;
      });
      if (flag) {
        this.axios({
          url: "/appcustomer/confirmIdentification.do",
          method: "post",
          params: {
            areaCode: forgetData.areaCode,
            channel: channel, //0   10 
            mobile: forgetData.mobile,
            smsCode: forgetData.vcode,
            type: 3
          }
        })
          .then(res => {
            if (channel === 0) {
              this.resetPassword();
            } else {
              this.stepNum = 1;
            }
          })
          .catch(err => {});
      }
    },
    resetPassword() {
      let identityData = this.$store.state.AccountLogin.identityData;
      this.$confirm(
        `${identityData.email}`,
        "",
        {}
      ).then(() => {
        this.axios({
          url: "/resetPassword",
          method: "post",
          params: {
            userNo: identityData.ebokerUsername
          },
          type: "hostEbroker"
        }).then(res => {
          this.stepNum = 0;
          this.$message({
            message: "!",
            type: "success"
          });
        });
      });
    },
    setAccount() {
      // institutionalAccount   ebokerUsername 
      let identityData = this.$store.state.AccountLogin.identityData;
      console.log(identityData);
      if (identityData.idCardCode) {
        this.accountSelectData = [];
        this.forgetData.mobile = identityData.mobile;
        this.forgetData.areaCode = identityData.areaCode;
        this.forgetData.account =
          identityData.ebokerUsername || identityData.institutionalAccount;
        if (identityData.ebokerUsername) {
          this.accountSelectData.push({
            value: identityData.ebokerUsername,
            label: ""
          });
        }
        if (identityData.institutionalAccount) {
          this.accountSelectData.push({
            value: identityData.institutionalAccount,
            label: ""
          });
        }
      } else {
        // app
        this.$message({
          message: "APP",
          type: "error"
        });
      }
    },
    close() {
      this.showForget = false;
      this.forgetData = {
        account: "",
        password1: "",
        password2: "",
        mobile: "",
        vcode: "",
        areaCode: ""
      };
      this.stepNum = 0;
    },
    show(type) {
      this.showForget = true;
      this.type = type;
      if (type === "forget") {
        this.setAccount();
      }
    }
  }
};
</script>

<style lang="scss" scoped>
$nColor: #767a92;

.forget {
  .back {
    @include add-size($font_size_14);
    line-height: 32px;
    cursor: pointer;
    padding: 0 18px;
    &:hover {
      color: $white;
    }
  }
  .step-title {
    @include add-size($font_size_18);
    color: $white;
    text-align: center;
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .forget-content {
    padding: 0 20px 20px;
    height: 316px;
    .text-content {
      text-indent: 2em;
      @include add-size($font_size_12);
      color: $white;
      line-height: 24px;
      max-height: 265px;
      overflow-y: auto;
    }
  }
  .ewm-img {
    text-align: center;
    margin-top: 30px;
    img {
      width: 160px;
      height: 160px;
    }
  }
  /deep/ .el-form {
    padding-top: 30px;
    .el-form-item {
      @include flex-center;
      width: 250px;
      border-bottom: 1px solid #576373;
      margin: 0 auto;
      height: 48px;
      position: relative;
    }
    .el-form-item__label {
      width: 54px;
      &::before {
        display: none;
      }
    }
    .el-input__inner {
      background-color: inherit;
      border: none;
      width: 190px;
      border-radius: 0;
      padding: 0 10px;
      color: $white;
    }
    .login-submit {
      width: 230px;
      height: 30px;
      line-height: 30px;
      margin: 36px auto 0;
      padding: 0;
      display: block;
      @include add-size($font_size_14);
    }
    .account {
      .icon {
        position: absolute;
        width: 14px;
        height: 13px;
        background: url("~@/assets/images/login/user-icon.png") 0 center
          no-repeat;
        background-size: 14px 13px;
        right: 6px;
        top: 50%;
        margin-top: -6px;
      }
    }
    .password {
      .icon {
        position: absolute;
        width: 18px;
        height: 11px;
        background: url("~@/assets/images/login/show-icon.png") 0 center
          no-repeat;
        background-size: 18px 11px;
        right: 5px;
        top: 50%;
        margin-top: -4px;
        cursor: pointer;
        &.password {
          background: url("~@/assets/images/login/hide-icon.png") 0 center
            no-repeat;
          background-size: 18px 8px;
          right: 5px;
        }
      }
    }
    .get-vcode {
      position: absolute;
      color: $activeColor;
      right: -4px;
      top: 0;
      cursor: pointer;
      &.has-send {
        color: $nColor;
        cursor: default;
      }
    }
  }
}
</style>
