<template>
  <div class="foundation">
    <div class="fund-data">
      <div class="fund-data-li">
        <span>()</span>
        <span>{{accountData.availCapital | moneyFormat}}</span>
      </div>
      <div class="fund-data-li">
        <span>()</span>
        <span>{{accountData.money | moneyFormat}}</span>
      </div>
      <div class="fund-data-li">
        <span>()</span>
        <span>{{accountData.expectProfit | moneyFormat}}</span>
      </div>
      <div class="fund-data-li">
        <span>()</span>
        <span>{{accountData.cumulative | moneyFormat}}</span>
      </div>
      <div class="tips-btn">
        <span @click="riskNotice = true"></span>
        <span @click="commonProblem = true"></span>
      </div>
    </div>
    <div class="item-type">
      <div class="item-type-li" :class="{active: currentType===0}" @click="currentType = 0"></div>
      <div class="item-type-li" :class="{active: currentType===1}" @click="currentType = 1"></div>
    </div>
    <el-table v-show="currentType===0" :data="listData.list" height="160" empty-text=" ">
      <el-table-column prop="fundName" label=""></el-table-column>
      <el-table-column prop="fundProfitRate" label="" align="center">
        <template slot-scope="scope">
          <div class="rise">+{{scope.row.fundProfitRate}}%</div>
        </template>
      </el-table-column>
      <el-table-column prop="raiseMoney" label="" align="center"></el-table-column>
      <el-table-column label="" align="center">
        <template slot-scope="scope">{{scope.row.raiseMoney - scope.row.surplusRaiseMoney}}</template>
      </el-table-column>
      <el-table-column prop="_residueTime" label="/" width="140px" align="center">
        <!-- <template slot-scope="scope">{{scope.row.raiseEndTime }}</template> -->
      </el-table-column>
      <el-table-column prop="lockDays" label="" align="center"></el-table-column>
      <el-table-column prop="interestRaisingInfo" label="" align="center"></el-table-column>
      <el-table-column prop="startMoney" label="" align="center"></el-table-column>
      <el-table-column prop="payBackInfo" label="" align="center"></el-table-column>
      <el-table-column prop="raiseEndTime" label="" align="center">
        <template slot-scope="scope">
          <div>{{scope.row.reachingInterestTime | dateFormat("yyyy-MM-dd")}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="serviceCharge" label="" align="center"></el-table-column>
      <el-table-column prop="fundUseExplain" label="" align="center">
        <template slot-scope="scope">
          <div class="use-for" :title="scope.row.fundUseExplain">{{scope.row.fundUseExplain}}</div>
        </template>
      </el-table-column>
      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <div
            class="action-btn"
            :class="{'no-residue': !scope.row._canBuy}"
            @click="buyIn(scope.row)"
            hover
          ></div>
        </template>
      </el-table-column>

      <template slot="append">
        <infinite-loading
          ref="infinite"
          @infinite="getList"
          :identifier="listData.identityId"
          :distance="20"
          forceUseInfiniteWrapper=".el-table__body-wrapper"
        >
          <loading></loading>
          <span slot="no-results">  </span>
          <span slot="no-more">  </span>
        </infinite-loading>
      </template>
    </el-table>
    <el-table v-show="currentType===1" :data="selfListData.list" :height="150" empty-text=" ">
      <el-table-column prop="fundName" label=""></el-table-column>
      <el-table-column prop="fundProfitRate" label="" align="center">
        <template slot-scope="scope">
          <div class="rise">+{{scope.row.fundProfitRate}}%</div>
        </template>
      </el-table-column>
      <el-table-column prop="money" label="()" align="center"></el-table-column>
      <el-table-column prop="expectProfit" label="()" align="center"></el-table-column>
      <el-table-column prop label="" align="center">
        <template slot-scope="scope">
          <div class="state" :data-status="scope.row.state">{{scope.row.state | stateName}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="expectStartTime" label="" align="center">
        <template slot-scope="scope">
          <div>{{scope.row.expectStartTime | dateFormat("yyyy-MM-dd")}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="endTime" label="" align="center">
        <template slot-scope="scope">
          <div>{{scope.row.endTime | dateFormat("yyyy-MM-dd")}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="buyTime" label="" align="center"></el-table-column>
      <el-table-column prop="fundOrderNo" label="" align="center"></el-table-column>

      <template slot="append">
        <infinite-loading
          ref="infinite"
          @infinite="getSelfList"
          :identifier="selfListData.identityId"
          :distance="20"
          forceUseInfiniteWrapper=".el-table__body-wrapper"
        >
          <loading></loading>
          <span slot="no-results">  </span>
          <span slot="no-more">  </span>
        </infinite-loading>
      </template>
    </el-table>
    <el-dialog
      class="fund-info-dialog"
      theame
      center
      :title="currentData.fundName"
      :modal="false"
      :visible="buyInShow"
      width="456px"
      top="15%"
      @close="buyInShow = false"
    >
      <div class="info-content">
        <div class="money-input">
          <div class="add-tip">
            <span class="add-tip-t">()</span>
            <p
              class="per"
            >{{currentData.startMoney > (currentData.raiseMoney - currentData.surplusRaiseMoney) ? (currentData.raiseMoney - currentData.surplusRaiseMoney) : currentData.startMoney}} {{currentData.startMultiple}}</p>
          </div>
          <el-input
            maxlength="11"
            v-model="buyInMoney"
            class="num-input"
            type="number"
            @blur="formatMoney"
            placeholder=""
          ></el-input>
          <div class="available-money">{{accountData.availCapital | moneyFormat}}</div>
        </div>
        <div class="buy-in-btn" @click="buyInInput" hover></div>
      </div>
    </el-dialog>
    <el-dialog
      class="tips-info-dialog"
      theame
      center
      title=""
      :modal="false"
      :visible="commonProblem"
      width="350px"
      top="15%"
      @close="commonProblem = false"
    >
      <div class="info-content">
        <p class="problem">1.?</p>
        <p class="answer">
          -
          
        </p>
        <p class="problem">2.?</p>
        <p class="answer">
          -
          T+1
          
        </p>
        <p class="problem">3.?</p>
        <p class="answer">
          
          
        </p>
        <p class="problem">4.?</p>
        <p class="answer">4.1</p>
        <p class="answer">4.2</p>
        <p class="answer">4.3</p>
        <p class="answer">4.4400-116-6913</p>
        <p class="problem">5.?</p>
        <p class="answer">T+1</p>
      </div>
    </el-dialog>
    <el-dialog
      class="tips-info-dialog"
      theame
      center
      title=""
      :modal="false"
      :visible="riskNotice"
      width="430px"
      top="15%"
      @close="riskNotice = false"
    >
      <div class="info-content">
        <p class="problem"></p>
        <p class="answer">
          
          
          
          
          
        </p>
        <p class="problem"></p>
        <p class="answer">
          /
          
          
          
        </p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import infiniteLoading from "vue-infinite-loading";

export default {
  components: {
    infiniteLoading
  },
  data() {
    return {
      currentType: 0,
      foundationData: [],
      accountData: {},
      listData: {
        list: [],
        identityId: 0,
        page: 1,
        size: 10
      },
      selfListData: {
        list: [],
        identityId: 0,
        page: 1,
        size: 10
      },
      timer: {},
      currentData: {},
      buyInShow: false,
      buyInMoney: "",
      riskNotice: false,
      commonProblem: false
    };
  },
  computed: {
    compareMoney() {
      if (this.currentData.startMoney) {
        let money =
          this.currentData.startMoney >
          this.currentData.raiseMoney - this.currentData.surplusRaiseMoney
            ? this.currentData.raiseMoney - this.currentData.surplusRaiseMoney
            : this.currentData.startMoney;
        return money;
      } else {
        return 0;
      }
    }
  },
  watch: {
    currentType(val) {
      if (val === 1 && this.selfListData.list.length <= 0) {
        this.selfListData.list = [];
        this.selfListData.identityId++;
        this.selfListData.page = 1;
      } else if (val === 0 && this.listData.list.length <= 0) {
        this.listData.list = [];
        this.listData.identityId++;
        this.listData.page = 1;
      }
    }
  },
  activated() {
    this.getAccountData();
    this.currentType = 0;
    this.listData.list = [];
    this.listData.identityId++;
    this.listData.page = 1;
  },
  deactivated() {
    this.selfListData.list = [];
    this.listData.list = [];
  },
  methods: {
    formatMoney() {
      // if (!money) {
      //   this.$message({
      //     message: "",
      //     type: "error"
      //   });
      // }
    },
    buyInInput() {
      try {
        let inputMoney = Number(this.buyInMoney);
        if(inputMoney <= 0)
          throw new Error("");
        // console.log(this.compareMoney);
        if (inputMoney < Number(this.compareMoney))
          throw new Error("");

        let moneySec = inputMoney - this.currentData.startMoney;
        let moneySecRes =
          Math.floor(moneySec / this.currentData.startMultiple) *
          this.currentData.startMultiple;

        inputMoney = Number(this.currentData.startMoney) + moneySecRes;
        if (inputMoney > this.accountData.availCapital)
          throw new Error("");

        this.axios({
          url: "/fund/project/saveBuyFund",
          method: "post",
          params: {
            buyMoney: inputMoney, // 
            id: this.currentData.id // id
          },
          type: "host2"
        }).then(res => {
          this.$message({
            message: "",
            type: "success"
          });
          this.buyInMoney = "";
          this.buyInShow = false;
          this.currentType = 1;
          this.selfListData.list = [];
          this.getAccountData();

          this.listData.list = [];
        });
      } catch (err) {
        this.$message({
          message: err.message,
          type: "error"
        });
      }
    },
    buyIn(row) {
      if (row.raiseMoney - row.surplusRaiseMoney <= 0) {
        this.$message({
          message: "",
          type: "error"
        });
        return;
      }
      if (!row._canBuy) {
        this.$message({
          message: "",
          type: "error"
        });
        return false;
      }
      this.currentData = row;
      this.buyInShow = true;
      this.buyInMoney = "";
    },
    getAccountData() {
      // host2
      this.axios({
        url: "/fund/project/selectAvailCapital",
        params: {},
        type: "host2"
      }).then(res => {
        this.accountData = res.data.ret || {};
      });
    },
    getList($state) {
      // /fund/project/selectListInfo?page=1&size=10
      let listData = this.listData;
      this.axios({
        url: "/fund/project/selectListInfoPC",
        params: {
          page: listData.page,
          size: listData.size
        },
        type: "host2"
      }).then(res => {
        let list = res.data.ret.list || [];
        if (list.length > 0) {
          list.forEach(i => ((i._residueTime = 0), (i._canBuy = true)));
          if (listData.page == 1) {
            listData.list = list;
          } else {
            listData.list = listData.list.concat(list);
          }
          listData.page++;
          this.setDataTime();
          $state.loaded();
        } else {
          $state.complete();
        }
      });
    },
    getSelfList($state) {
      // /fund/project/selectListInfo?page=1&size=10
      // fund/project/selectProjectList
      let selfListData = this.selfListData;
      this.axios({
        url: "/fund/project/selectProjectList",
        params: {
          page: selfListData.page,
          size: selfListData.size
        },
        type: "host2"
      }).then(res => {
        let list = res.data.ret.list || [];
        if (list.length > 0) {
          if (selfListData.page == 1) {
            selfListData.list = list;
          } else {
            selfListData.list = selfListData.list.concat(list);
          }
          selfListData.page++;
          $state.loaded();
        } else {
          $state.complete();
        }
      });
    },
    setDataTime() {
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        regTime();
        this.setDataTime();
      }, 1000);
      let _this = this;
      regTime();
      function regTime() {
        _this.listData.list.forEach(a => {
          // 
          if (a.raiseMoney - a.surplusRaiseMoney <= 0) {
            a._canBuy = false;
          }
          let beginTime = a.raiseBeginTime.replace(/-/g, "/");
          let endTime = a.raiseEndTime.replace(/-/g, "/");
          let now = new Date().getTime();
          let end = new Date(endTime).getTime();
          let begin = new Date(beginTime).getTime();
          let res = "",
            resText = "";
          if (begin > now) {
            res = begin - now;
            resText = "";
            a._canBuy = false;
          } else if (end > now) {
            res = end - now;
            resText = "";
          } else {
            a._residueTime = "";
            a._canBuy = false;
            return;
          }
          let day = Math.floor(res / (24 * 60 * 60 * 1000));
          let level1 = res % (24 * 60 * 60 * 1000);
          let hh = Math.floor(level1 / (60 * 60 * 1000));
          let level2 = level1 % (60 * 60 * 1000);
          let mm = Math.floor(level2 / 60 / 1000);
          let level3 = level2 % (60 * 1000);
          let ss = Math.floor(level3 / 1000);
          a._residueTime = `${day}${hh}${mm}${ss}${resText}`;
        });
      }
    }
  },
  filters: {
    // residueTime(val) {
    //   let now = new Date().getTime();
    //   let end = new Date(val).getTime();
    //   let resi = new Date(end - now).Format("ddhhmmss");
    //   return resi;
    // }
    stateName(val) {
      const state = {
        "-1": "",
        "1": "",
        "2": "",
        "3": "",
        "4": "",
        "5": ""
      };
      return state[val] || "";
    }
  },
  created() {},
  mounted() {},
  updated() {},
  destroyed() {}
};
</script>
<style lang='scss' scoped>
//@import url(); css
$borColor: #323840;
.foundation {
  padding-left: 20px;
  padding-right: 20px;
  .fund-data {
    @include flex-center;
    padding: 20px 0;
    color: $white;
    position: relative;
    @include add-size($font_size_14);
    .fund-data-li {
      margin-right: 72px;
      span:first-child {
        margin-right: 4px;
      }
    }
    .tips-btn {
      position: absolute;
      right: 0;
      top: 20px;
      span {
        cursor: pointer;
        color: #178de7;
        margin-left: 24px;
        &:hover {
          opacity: 0.8;
        }
      }
    }
  }
  .item-type {
    @include flex-center;
    .item-type-li {
      padding-bottom: 10px;
      margin-right: 53px;
      position: relative;
      cursor: pointer;
      &.active {
        color: $activeColor;
        &::after {
          content: "";
          position: absolute;
          width: 30px;
          height: 2px;
          left: 50%;
          bottom: 0;
          margin-left: -15px;
          background-color: $activeColor;
        }
      }
    }
  }
  /deep/ .el-table {
    .action-btn {
      width: 48px;
      height: 22px;
      border-radius: 6px;
      line-height: 22px;
      margin: 0 auto;
      color: $white;
      @include linear;
      cursor: pointer;
      &.no-residue {
        background: #999;
      }
    }
    .use-for {
      @include single-line-ellipsis;
    }
    .state {
      &[data-status="1"] {
        color: $red;
      }
      &[data-status="5"] {
        color: #178de7;
      }
    }
  }
  .fund-info-dialog {
    .info-content {
      color: $defaultColor;
      padding: 36px 38px;
      .money-input {
        border: 1px solid $borColor;
        border-radius: 6px;
        padding: 30px;
        @include add-size($font_size_12);
        .add-tip {
          line-height: 18px;
          .add-tip-t {
            @include add-size($font_size_18);
            color: $white;
          }
          .per {
            line-height: 24px;
          }
        }
        /deep/.num-input {
          margin-top: 10px;
          input {
            padding-left: 70px;
            background-color: inherit;
            border: none;
            border-radius: 0;
            border-bottom: 1px solid $borColor;
            width: 270px;
            @include add-size($font_size_16);
            color: $white;
          }
          &:before {
            content: "HK$";
            color: $white;
            @include add-size(30px);
            position: absolute;
            left: 0;
            top: 0;
          }
        }
        .available-money {
          padding-top: 10px;
        }
      }
      .buy-in-btn {
        width: 140px;
        height: 30px;
        cursor: pointer;
        @include add-size($font_size_14);
        @include linear;
        color: $white;
        line-height: 30px;
        text-align: center;
        border-radius: 6px;
        margin: 49px auto 36px;
      }
    }
  }
  .tips-info-dialog {
    .info-content {
      @include add-size($font_size_12);
      color: $white;
      padding: 4px 20px 20px;
      line-height: 16px;
      p {
        margin-bottom: 6px;
      }
      .problem {
        margin-top: 16px;
      }
    }
  }
}
</style>