<template>
  <div class="fund-save">
    <div class="erborker" v-if="currentType === 0">
      <div class="left-qrcode">
        <div class="tips-title"></div>
        <div class="tips-text">PCAPP</div>
        <div class="qrcode-box">
          <img src="http://www.easy913.com/image/erweima118.png" alt="" />
        </div>
        <div class="img-tips">APP</div>
      </div>
      <div class="right-app-img">
        <img class="app-img" src alt />
      </div>
    </div>
    <div class="org" v-if="currentType === 1">
      <div class="left-draw draw-content">
        <bank-select wd :bankDataArr="bankDataArr" v-model="currentBank" @add-card="addCard"></bank-select>
        <div class="money-input">
          <div class="input-t">
            <div class="input-t_text"></div>
            <div class="draw-type">
              <span></span>
              <span></span>

              <!-- <el-select v-model="drawData.type">
                <el-option
                  :label="op.label"
                  :value="op.value"
                  v-for="op in drawData.typeSelect"
                  :key="op.value"
                ></el-option>
              </el-select> -->
            </div>
          </div>
          <el-input
            class="money-in rmb"
            v-if="drawData.type == 2"
            v-model="drawData.money"
            type="number"
            placeholder=""
            maxlength="10"
          ></el-input>
          <el-input
            class="money-in hk"
            v-if="drawData.type == 0"
            v-model="drawData.money"
            type="number"
            maxlength="10"
            placeholder=""
          ></el-input>
          <div class="res-money">
            <div
              class="total-money"
            > {{(drawData.type == 0 ? accountInfo.withDrawCashBalance : accountInfo.withDrawCashBalance * drawData.rate) | moneyFormat}}{{drawData.type == 0 ? '':''}}</div>
          </div>
        </div>
        <el-button class="action" @click="drawMoneyRequset"></el-button>
      </div>
      <div class="right-rule">
        <div class="draw-tips">
          <p class="tips-title"></p>
          <p>11-2</p>
          <p>210</p>
          
          <!-- <p>350</p> -->
          <!-- <p v-if="drawData.type == 2">410</p> -->
          <!-- <p v-if="drawData.type == 0">4</p> -->
          <!-- <p>4</p> -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import bankSelect from "@/components/deal/bank-select";

export default {
  props: { accountInfo: Object },
  components: {
    bankSelect
  },
  data() {
    return {
      drawData: {
        type: 2,
        money: "",
        typeSelect: [
          {
            label: "",
            value: 0
          },
          {
            label: "",
            value: 2
          }
        ],
        rate: ""
      },
      rateData: {},
      bankDataArr: [],
      currentBank: {}
    };
  },
  computed: {
    currentType() {
      //   0 erbroker  1 
      let currentType = this.$store.state.system.dealBar.currentType;
      return currentType;
    }
  },
  watch: {
    currentType(val) {
      if (this.bankDataArr.length > 0 && val == 1) {
        setTimeout(() => {
          this.bankDataArr = JSON.parse(JSON.stringify(this.bankDataArr));
        }, 300);
      }
    }
  },
  mounted() {},
  activated() {
    this.getBankCard();
    this.getRate();
  },
  methods: {
    getRate() {
      // 
      this.axios({
        url: "/fund/selectRate",
        method: "get",
        params: {},
        type: "host2"
      }).then(res => {
        this.rateData = res.data.ret;
        this.drawData.rate = this.rateData.ukToRmb;
      });
    },
    addCard() {
      // this.$store.dispatch("CHANGE_MENU", {
      //   target: 2,
      //   timestamp: new Date().getTime()
      // });

      this.$store.dispatch("CHANGE_ROUTER_TARGET", {
        a: 10,
        timestamp: new Date().getTime() - 1000 * 10
      });
      setTimeout(() => {
        this.$router.push({ path: "wd", query: { target: "myBankCard" } });
      }, 100);
    },
    drawMoneyRequset() {
      if (this.drawData.money <= 0) {
        this.$message({
          message: "",
          type: "error"
        });
        return false;
      }
      if (!this.currentBank.accountNo) {
        this.$message({
          message: "",
          type: "error"
        });
        return false;
      }
      if (!this.currentBank.isHKCard == "0" && this.drawData.money <= 10) {
        this.$message({
          message: "10",
          type: "error"
        });
        return false;
      }
      // console.log(this.currentBank);
      // /order/message/saveRecord
      this.axios({
        url: "/order/message/saveRecord",
        method: "post",
        params: {
          money:
            this.drawData.type == 2
              ? Number(this.drawData.money / this.drawData.rate).toFixed(3)
              : this.drawData.money,
          bankAccount: this.currentBank.accountNo,
          companyBankId: this.currentBank.cardBindId,
          incomeType: 1,
          isHKCard: this.currentBank.isHKCard,
          currency: 0,
          type: 1, //,0:ebroker 1 
          rate: this.drawData.type == 2 ? this.drawData.rate : 1,
          chooseCurrency: this.drawData.type
        },
        type: "host2"
      }).then(res => {
        this.$message({
          message: "",
          type: "success"
        });
        this.$emit("cancelOrder"); // 
        this.drawData.money = "";
      });
    },
    getBankCard() {
      this.axios({
        url: "/order/message/selectBankBind",
        method: "get",
        params: {
          type: 1
        },
        type: "host2"
      }).then(res => {
        this.bankDataArr = res.data.ret;
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.fund-save {
  @include flex-box;
  .left-qrcode {
    color: $white;
    padding: 20px 44px 0;
    .tips-text {
      margin-top: 12px;
    }
    .qrcode-box {
      width: 160px;
      height: 160px;
      margin: 20px auto 0;
      img {
        width: 100%;
        height: 100%;
        margin: 0;
      }
    }
    .img-tips {
      text-align: center;
      margin-top: 19px;
    }
  }

  .org {
    @include flex-box;
  }
  .draw-content {
    color: $defaultColor;
    padding: 0 20px;
    width: 340px;
    /deep/.bank-select {
      margin: 10px auto;
      background: inherit;
      border: 1px solid $defaultColor;
      border-radius: 6px;
      width: 300px;
      height: 50px;
      &[wd] {
        > .bank-item {
          background: inherit;
        }
        .bank-ul {
          max-height: 150px;
          top: 60px;
          padding: 0;
        }
        .bank-item {
          height: 50px;
          .bank-icon {
            width: 40px;
            height: 40px;
          }
          .bank-name {
            @include add-size($font_size_14);
          }
          .bank-account {
            @include add-size($font_size_12);
          }
        }
      }
    }
    .money-input {
      margin: 0 auto;
      background: inherit;
      border: 1px solid $defaultColor;
      border-radius: 6px;
      padding: 10px;
      @include add-size($font_size_14);
      .input-t {
        @include flex-center;
        justify-content: space-between;
        margin-bottom: 6px;
        .draw-type {
          span {
            margin-right: 6px;
          }
          /deep/.el-input__inner {
            width: 100px;
            background-color: inherit;
            border: none;
            border-radius: 0;
            border: 1px solid #576373;
            color: $defaultColor;
            line-height: 30px;
            height: 30px;
          }
          /deep/ .el-input__icon {
            line-height: 30px;
          }
        }
      }
      .money-in {
        position: relative;
        &.rmb {
          &::before {
            content: "";
            position: absolute;
            color: $white;
            left: 0;
            top: 0;
            @include add-size(24px);
            line-height: 36px;
          }
        }
        &.hk {
          &::before {
            content: "HK$";
            position: absolute;
            color: $white;
            left: 0;
            top: 0;
            @include add-size(24px);
            line-height: 36px;
          }
        }
        /deep/.el-input__inner {
          padding-left: 60px;
          background-color: inherit;
          height: 36px;
          border: none;
          border-radius: 0;
          border-bottom: 1px solid #576373;
          @include add-size($font_size_16);
          background-size: 24px 33px;
          color: $white;
        }
      }

      .res-money {
        margin-top: 10px;
        @include flex-center;
        justify-content: space-between;
        .tips {
          cursor: pointer;
          &:hover {
            color: $activeColor;
          }
        }
      }
    }
    .action {
      display: block;
      margin: 10px auto;
      height: 30px;
      @include add-size($font_size_14);
    }
  }
  .draw-tips {
    height: 411px;
    line-height: 28px;
    padding: 30px 20px;
    color: $defaultColor;
    .tips-title {
      margin-bottom: 10px;
    }
  }
}
</style>